import mixin from '@/common/mixin';
import dragball from "@/components/drag-ball/drag-ball.vue";
import sPopup from '@/components/s-popup/index.vue';
export default {
	components: {
		sPopup,
		dragball
	},
	onLoad() {
		console.log(this.rentData[this.currentIndex]);
	},

	data() {
		return {
			img:'http://taotao111.test.upcdn.net/home_icon_gwcc.png',
			categories: ['全部', '棉被', '枕头', '床单', '被套'],
			currentIndex: 0,
			currentGoodIndex: 0,
			visible: false,
			number: 0,
			times: ['一学期', '一年'],
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			rentData: [
				[
					{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: '￥36.00' },
					{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: '￥36.00' },
					{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: '￥36.00' },
					{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: '￥36.00' },
					{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: '￥36.00' },
					{ image: 'http://taotao111.test.upcdn.net/goods.png', title: '保暖棉被租用一床1.5X2.0两斤重 租半年', time: '一学期', deposit: '押金：50元', price: '￥36.00' },
