				[],
				[],
				[],
				[]
			]
		};
	},
	computed: {
		showSubcategory() {
			return this.rentData[this.currentIndex];
		}
	},
	mixins: [mixin],
	onPullDownRefresh() {
		this.getStatisticsService();
		this.getIndexRumorList();
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		delscroll() {
			plus.webview.currentWebview().setStyle({
				scrollIndicator: 'none'
			});
		},
		showDetail() {
			this.visible = true;
		},
		itemClick(index) {
			this.currentIndex = index;
			// this.$emit('selectItem', index);
		},
		itemGoodClick(index) {
			this.currentGoodIndex = index;
			// this.$emit('selectItem', index);
		},
		selectItem(index) {
			this.currentIndex = index;
		},
		plus() {
			this.number++;
		},
		decre() {
			if (this.number > 0) {
				this.number--;
			}
		},
		closeMark() {
			this.visible = false;
		},
		goCart() {
			uni.navigateTo({
				url: '../cart/index'
			});
		}
	}
};
